import { Component, OnInit, ViewChild } from '@angular/core';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';
import { MeetingService } from 'src/app/api/controllers/Meeting';
import * as moment from 'moment';
import _moment from "moment-timezone";
import { MatDialog } from '@angular/material/dialog';
import { CalendarOptions } from '@fullcalendar/angular';
import { MeetingEditDisplayComponent } from '../meeting-setting/meeting-edit-display/meeting-edit-display.component';

@Component({
  selector: 'app-meeting-calendar',
  templateUrl: './meeting-calendar.component.html',
  styleUrls: ['./meeting-calendar.component.scss']
})

export class MeetingCalendarComponent implements OnInit {
  loadingData = false;
  calendarOptions: CalendarOptions = {
    initialView: 'dayGridMonth',
    headerToolbar: {
      left: 'prev,next',
      center: 'title',
      right: 'dayGridMonth,timeGridWeek,timeGridDay,listWeek'
    },
    aspectRatio: 2,
    resourceAreaHeaderContent: 'Facility',
    selectable : true, 
    select : this.appointmentAdd.bind(this),
    eventClick: this.appointmentClick.bind(this),
    schedulerLicenseKey: "CC-Attribution-NonCommercial-NoDerivatives"
  };

  timeZone:any;

  constructor(
    private meetingService:MeetingService,
    private sharedService:SharedService,
    private sessionService:SessionService,
    public dialog: MatDialog
  ) {}
  
  appointmentClick(info) {
    if(this.sessionService.isAdmin()) {
      let data:any = {id:info.event.extendedProps.meetingId,type:'edit',from:'calendar'};
      const dialogRef = this.dialog.open(MeetingEditDisplayComponent, {
        panelClass: 'material-dialog-big',
        height:'inherit',
        width:'auto',
        data:data
      });
      dialogRef.afterClosed().subscribe(result => {
        if(result) {
          this.getMeetingList();
        }
      });
    }
  }

  appointmentAdd(event) {
    if(this.sessionService.isAdmin()) {
      let data = {type:'create',from:'calendar',event:event} 
      const dialogRef = this.dialog.open(MeetingEditDisplayComponent, {
        panelClass: 'material-dialog-big',
        height:'inherit',
        width:'auto',
        data:data
      });
      dialogRef.afterClosed().subscribe(result => {
        if(result) {
          this.getMeetingList();
        }
      });
    }
  }

  getMeetingList() {
    let params = {
      apartmentId : this.sessionService.apartmentId
    }
    this.meetingService.getMeetingByApartmentId(params).subscribe((res : any) => {
      if(res.length > 0) {
        res.forEach((data)=>{
          data.title = data.subject,
          data.start =  `${moment(data.meetingDate).format('YYYY-MM-DD')} ${data.fromTime}`
          data.end =  `${moment(data.meetingDate).format('YYYY-MM-DD')} ${data.toTime}`
          data.color = 'red'
        })
        this.calendarOptions.events = res;
      }
      this.loadingData = true;
    })
  }

  ngOnInit(): void {

    this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone )

    this.getMeetingList();
  }

}
